@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form--container{color:#fff;background-color:#3453a3;font-family:"Poppins",sans-serif;width:400px;border-radius:7px;margin-left:auto;margin-right:auto;margin-bottom:50px}.form--container h3{font-weight:700;margin-bottom:30px}.form--container a{text-decoration:none;font-weight:700;color:#fff;transition:0.3s ease;display:inline-block;margin-top:35px;margin-bottom:10px}.form--hidden-container{width:85%;margin-left:auto;margin-right:auto;padding:30px 0px}.form--btn-submit{width:340px;background-color:#fff;color:#001a5c;margin-top:15px;border-radius:4px}.form--input-height{height:50px}.form--input{background-color:transparent;color:#fff;border:1px solid #dbdbdb}.form--input::-webkit-input-placeholder{background-color:transparent;color:#fff}.form--input:-ms-input-placeholder{background-color:transparent;color:#fff}.form--input::after,.form--input::before,.form--input:hover,.form--input::placeholder{background-color:transparent;color:#fff}.form--btn-signup{height:50px;background-color:transparent;border:1px solid #fff}.form--btn-submit:hover{background-color:#dedaf8;color:black}

.alert--container{width:650px;background-color:#ffede2;color:#001a5c;margin:20px auto}.alert--hidden-container{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:20px 35px}.alert--hidden-container button{height:50px;background-color:#3453a3;border:none;color:#fff;border-radius:4px;padding:12px}.alert--tip{border:1px solid #edbb9c;border-radius:4px;width:9%;text-align:center;color:#af6c43;font-weight:bold;background-color:#fff}.alert--text{width:80%}.alert--text a{text-decoration:none;color:#001a5c;text-decoration:underline;-webkit-transform:0.3s ease;transform:0.3s ease}.alert--text a:hover{color:#3453a3}

.language--container{display:flex;justify-content:center}.language--container p{display:inline-block;margin-top:5px;margin-right:3px}

